import React, { useEffect } from 'react';
import HomePage from './Components/HomePage/HomePage';
import TheGymLog from './Components/TheGymLog/TheGymLog';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  BrowserRouter,
  HashRouter
} from "react-router-dom";
import './app.css';


function App() {

  console.log(window.location.pathname);
  console.log(window.location.pathname.match(/^\/PersonalWebsiteV2\/TheGymLog/));
  if (window.location.pathname.match(/^\/PersonalWebsiteV2\/TheGymLog/)) {
    console.log('The Gym Log');
    return (
      <TheGymLog />
    )
  } else {
    return (
      <HashRouter basename="/PersonalWebsiteV2">
        <Redirect from="/" to="/home/" />
        <Redirect from="/" to="/home" />
        <Route exact path="/home" component={HomePage} />
      </HashRouter>
    );
  }
}

export default App;
